

body {
  margin: 0;
  font-family: Roboto,sans-serif;
  font-style: normal;
  -webkit-font-smoothing : "Roboto", sans-serif ;
  -moz-osx-font-smoothing :"Roboto", sans-serif ;
  font-style: normal;
}
h1,
h2,
h3,
h4,
h5,
h6,
p{
  margin: 0;
}
:root{
  --primary: #5e3bee;
  --heading-color:#282938;
  --bg-shade:#F5FCFF;
  --github:#e62872;
  --darkblue:#1c1e53;
  --black:#000;
  --white :#ffffff;
}
.btn {
  font-family: "Roboto";
  display: inline-block;
  padding: 13px 32px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  border-radius: 5;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: background-color 0.5s;
  -webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
}
.btn-outline-primary {
  color: var(--primary);
  background-color: var(--white);
  border: 1px solid var(--primary);
}
.btn-outline-primary:hover {
  color: var(--white);
  background-color: var(--primary);
}
.btn-primary {
  color: var(--white);
  background-color: var(--primary);
  border: 1px solid var(--primary);
}
.btn-primary:hover {
  color: var(--primary);
  background-color: var(--white);
}
.btn-github {
  color: var(--white);
  background-color: var(--github);
  border: var(--github);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.95);
  gap: 6px;
}
.btn-github:hover {
  color: var(--github);
  background-color: var(--white);
}
/* Global Css Ends */

/* Main in Heading  */

/* Heading  1 */
h1{
  font-size: 56px;
  font-weight: 700;
  line-height: 67px;
}
/* Heading 2 */
h2{
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
}

/* Heading 3 */
h2{
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

/* Paragrah text large medium small */
/* Body 1 */

.text-lg{
  color: var(--darkblue);
  font-size: 18px;
  font-style: normal;
  font-weight: 400px;
  line-height: 27px;
}
/* Body2 */
.text-md{
  color: var(--darkblue);
  font-size: 16px;
  font-style: normal;
  font-weight: 400px;
  line-height: 24px;
}
/* Body3 */
.text-sm{
  color: #000;
  color: var(--black);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px; 
}

/* Selction Title */
/* Skil & aboutme*/

.section-title{
  color: var(--heading-color) ;
  font-size: 21px;
  text-align: center ;
  font-style: normal;
  font-weight: 600;
  line-height:32px;
  display: flex;
  align-items: flex-start;

}

/* Portfolio Testimonial & Contact Me */
.sub--title{
