  font-size: 26px;
  font-weight: 600;
  line-height: 24px;
}
/* Section Title Ends */
/* Navbar Style start */
.navbar {
  align-items: center;
  background: #fff;
  background: var(--white);
  box-shadow: 0 5.333px 80px 0 rgba(0,0,0,.1);
  display: flex;
  justify-content: space-between;
  left: 0;
  padding: 15px 85.333px;
  position: fixed;
  right: 0;
  top: 0;
}
.navbar--items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
}
.navbar--items ul > li > a {
  text-decoration: none;
}
/* Navbar Content */
.navbar--content {
  color: var(--darkblue);
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
}
.navbar--active-content {
  color: var(--primary);
}
/*Navbar Style Ends */
/* Hero Section Style */
.hero--section {
  grid-gap: 32px;
  align-items: center;
  background-color: #f5fcff;
  background-color: var(--bg-shade);
  display: grid;
  gap: 32px;
  grid-template-columns: repeat(2,1fr);
  justify-content: space-between;
  padding: 133.333px 85.333px;

}
.hero--section--content--box{
  display: flex;
  flex-direction: column;
  align-items:flex-start;
  gap: 32px;
}
.hero--section-content{
  display: flex;
  flex-direction: column;
  align-items:flex-start;
  gap:21.333px;
}
.hero--section--content--box > button {
  margin: 21.333px;
}
.hero--section--title{
  color: #282938;
  color: var(--heading-color);
  font-size: 74.667px;
  font-weight: 700;
  line-height: 90px;
}
.hero--section-title-color{
 color: var(--primary);
}
.hero-section-description{
  color: var(--darkblue);
  font-size:24px;
  font-weight:400;
  line-height:36px;
  
}
.hero--section-description{
  font-size: 25px;
  line-height: 40px;
  text-decoration: none;
  text-indent: 50px;
  text-align: justify;
  border: 1px solid gray;
  padding: 40px;
  background-color: var();
  
  
}
.hero--section--img{
  display: flex

}
.hero--section--img > img{
  
  width: 100%;
  height: 100%;
  
  margin-top: 60px;

  
}

/* Skills Section Style card */

.skill--section{
